*{
  box-sizing: border-box;
}
body{
  font-family: Arial;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  
}
.block {
  padding-top: 1px;
  height: 136px;
  width: 104px;
  background-color: rgba(95, 94, 94, 0.062);
  border-radius: 10px;
  justify-content: center;
  align-items:center;
  cursor: pointer;
}
.img-content{
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.text{
color: black;
font-size: 14px;
margin-bottom: 0px;
margin-left: 3px;
}
.img{
height: 82px;
border-bottom-right-radius: 15px ;
}


.catigories-slider {

  // .catigories-slider__swiper

  &__swiper {
    .swiper-button-disabled{
      display: none;
    }
    position: relative;
  }

  // .catigories-slider__container

  &__container {
  }

  // .catigories-slider__wrapper

  &__wrapper {
  }

  // .catigories-slider__slide

  &__slide {
    width: 104px;
  }
}

.catigories-slider {
  
  // .catigories-slider__arrow-next

  &__arrow-next {
    
    top: 60px;
    right:  -10px;
    &:hover{
      margin-bottom: 3px;
    }
    @media (max-width:1250px) {
      right: 0;
    }
  }
  &__arrow-prev {
    top: 60px;
    left:-10px;
    transform: rotate(180deg);
    &:hover{
      margin-bottom: 3px;
      
    }
    @media (max-width:1250px) {
      left: 0;
    }
  }
  &__arrow-prev, &__arrow-next{
    position: absolute;
    width: 32px;
    height: 32px;
    background-color: lightgrey;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    z-index: 1;
    cursor: pointer;
    transition-duration: 0.3s;
    
  }
  
}


